<template>
    <div class="h-full">
        <div class="panel-nav">
            <el-breadcrumb separator="/" class="m-l-lg">
                <el-breadcrumb-item>
                {{$t('teacher.title')}}
                </el-breadcrumb-item>
            </el-breadcrumb>
            <el-button class="btn btn-gradient m-r-lg" @click="openAddDialog('add')">
                {{$t('teacher.add')}}
            </el-button>
        </div>
        <div class="panel-body-list clearfix">
            <list-table
                :data="listData"
                :fields="fields"
                :operators="operators"
                class="wrapper-md"
                :operators-title="$t('operate.title')">
                <div slot="btns-bottom" slot-scope="slotData">
                    <el-button class="btn btn-manage" @click="removeItems(slotData)">{{$t('operate.remove')}}</el-button>
                </div>
                <div slot="btns">
                    <button class="fa fa-refresh btn-refresh" @click="refresh"></button>
                </div>
            </list-table>
        </div>
        <edit-dialog
            :visible="addForm.visible"
            :data="addForm.data"
            :title="addForm.title"
            :fields="addForm.fields"
            class="dialog-xl"
            @close="addCancel"
            @confirm="addConfirm"
        ></edit-dialog>
        <tip-dialog
            :visible="resetForm.visible"
            :message="resetForm.message"
            :title="resetForm.title"
            @close="resetCancel"
            @confirm="resetConfirm"
            :type="resetForm.type"
        ></tip-dialog>
        <tip-dialog
            :visible="deleteData.visible"
            :message="deleteData.message"
            :title="deleteData.title"
            @close="deleteData.close"
            @confirm="deleteData.confirm"
            :type="deleteData.type"
            ></tip-dialog>
    </div>
</template>
<script>
    import listTable from '../table/table'
    import editDialog from '../common/editDialog'
    import tipDialog from '../common/tipDialog'
    import _ from 'lodash'

    export default {
        created() {
            this.getListData()
        },
        data() {
            return {
                listData: [],
                fields: [{
                    name: 'username',
                    showName: this.$t('teacher.username')
                }, {
                    name: 'name',
                    showName: this.$t('teacher.name')
                }, {
                    name: 'mobileNo',
                    showName: this.$t('teacher.mobileNo')
                }, {
                    name: 'emailAddress',
                    showName: this.$t('teacher.email')
                }],
                operators: [{
                    name: this.$t('operate.edit'),
                    handler: this.edit
                }, {
                    name: this.$t('operate.reset'),
                    handler: this.reset
                }, {
                    name: this.$t('operate.remove'),
                    handler: this.remove
                }],
                addForm: {
                    title: this.$t('operate.add'),
                    visible: false,
                    data: {
                        type: 'add',
                        username: '',
                        name: '',
                        email: '',
                        mobileNo: '',
                        password: '',
                        rePassword: '',
                        roleList: [{id:2}]
                    },
                    fields: [{
                        name: 'username',
                        showName: this.$t('teacher.number'),
                        type: 'text',
                        required: true,
                        rules: {
                            max() {
                                return this.data.username.length <= 10
                            }
                        },
                        rulesMsg: {
                            max: this.$t('teacher.numberMax')
                        }
                    }, {
                        name: 'name',
                        showName: this.$t('teacher.name'),
                        required: true,
                        type: 'text'
                    }, {
                        name: 'emailAddress',
                        showName: this.$t('teacher.email'),
                        type: 'text'
                    }, {
                        name: 'mobileNo',
                        showName: this.$t('teacher.telephone'),
                        type: 'text'
                    }, {
                        name: 'password',
                        showName: this.$t('teacher.password'),
                        type: 'password',
                        show() {
                            return this.data.type === 'add'
                        },
                        required: true
                    }, {
                        name: 'rePassword',
                        showName: this.$t('teacher.rePassword'),
                        type: 'password',
                        required: true,
                        show() {
                            return this.data.type === 'add'
                        },
                        rules: {
                            rePassword() {
                                return this.data.rePassword === this.data.password
                            }
                        }
                    }]
                },
                resetForm: {
                    title: this.$t('teacher.reset'),
                    visible: false,
                    message: '',
                    type: 'reset'
                },
                deleteData: {
                    visible: false,
                    title: this.$t('operate.deleteTitle'),
                    message: '',
                    type: 'delete',
                    confirm: () => {
                        this.$httpMethods.user.removeUsers(this.deleteData.ids).then(res => {
                            if(res && res.status === 200) {
                                this.deleteData.visible = false
                                this.getListData()
                                this.$notify({
                                    type: 'success',
                                    title: this.$t('common.success'),
                                    message: this.$t('operate.message.删除成功')
                                })
                            }
                            
                        })
                    },
                    close: () => {
                        this.deleteData.visible = false
                    }
                }
            }
        },
        methods: {
            getListData() {
                this.$httpMethods.user.queryUsersByRoleType(2).then(res => {
                    if(res && res.status ===200 ) {
                        this.listData = res.data
                    }
                })
            },
            openAddDialog(type, data) {
                this.addForm.visible = true
                this.addForm.data.type = type

                if(type === 'edit') {
                    this.addForm.data = _.clone(data.$$src)
                }
            },
            edit(obj) {
                const id = obj.$$src.id
                //todo 根据id查询数据
                this.addForm.title = this.$t('operate.edit')
                this.openAddDialog('edit', obj);
            },
            refresh() {
                this.$httpMethods.user.queryUsersByRoleType(2).then(res => {
                    if(res && res.status === 200) {
                        this.listData = res.data
                        this.$notify({
                            type: 'success',
                            title: this.$t('common.success'),
                            message: this.$t('operate.message.refresh')
                        })
                    }
                })
            },
            reset(data) {
                this.resetForm.message = this.$t('operate.message.reset', {name: data.$$src.name})
                this.resetForm.visible = true
                this.resetForm.id = data.$$src.id
            },
            remove(data) {
                this.deleteData.message = this.$t('operate.message.delete', { name: data.$$src.name })
                this.deleteData.visible = true
                this.deleteData.ids = [data.$$src.id]
            },
            removeItems(data) {
                if(data.tableData && data.tableData.selection) {
                    let ids = []
                    data.tableData.selection.forEach(row => {
                        ids.push(row.$$src.id)
                    })
                    if(ids.length === 1) {
                        this.deleteData.message = this.$t('operate.message.delete', { name: this.selectRows[0].$$src.name })
                    } else {
                        this.deleteData.message = this.$t('operate.message.deleteItems', { length: ids.length })
                    }
                    this.deleteData.visible = true
                    this.deleteData.ids = ids;
                }
            },
            addCancel() {
                this.addForm.visible = false
                this.addForm.title = this.$t('operate.add')
                this.resetAddForm()
            },
            addConfirm(data) {
                let method = ''
                method = this.addForm.data.type === 'add'?'addUser':'modifyUser'
                this.$httpMethods.user[method](data).then(res => {
                    if(res && res.status === 200) {
                        this.addForm.visible = false
                        this.addForm.title = this.$t('operate.add')
                        this.resetAddForm()
                    }
                })
            },
            resetAddForm() {
                this.addForm.data = {
                    type: 'add',
                    username: '',
                    name: '',
                    emailAddress: '',
                    mobileNo: '',
                    password: '',
                    rePassword: '',
                    roleList: [{
                        id: 2,
                        description: '',
                        name: '',
                        sysName: ''
                    }]
                }
            },
            resetCancel() {
                this.resetForm.visible = false
            },
            resetConfirm() {
                this.$httpMethods.user.resetPassword(this.resetForm.id).then(res => {
                    if(res && res.status === 200) {
                        this.resetForm.visible = false
                        this.$notify({
                            type: 'success',
                            title: this.$t('common.success'),
                            message: this.$t('operate.message.resetSuccess')
                        })
                    }
                })
            }
        },
        components: {
            listTable,
            editDialog,
            tipDialog
        }
    }
</script>
<i18n>
    {
        "zh": {
            "teacher": {
                "title": "教师管理",
                "add": "添加教师",
                "reset": "重置密码",
                "username": "用户名",
                "department": "部门",
                "number": "教师编号",
                "name": "姓名",
                "department": "部门",
                "position": "职称",
                "assistant": "助教",
                "lecturer": "讲师",
                "associateProfessor": "副教授",
                "professor": "教授",
                "email": "电子邮箱",
                "telephone": "联系电话",
                "mobileNo": "手机号码",
                "password": "密码",
                "rePassword": "确认密码",
                "numberMax": "教师编号长度不允许超过10"
            },
            "operate": {
                "add": "添加",
                "title": "操作",
                "remove": "删除",
                "edit": "编辑",
                "reset": "重置密码",
                "deleteTitle": "确认删除",
                "message": {
                    "delete": "确定要删除 \"{name}\" 教师吗？",
                    "deleteItems": "确定要删除这 \"{length}\" 项吗？",
                    "reset": "确定要重置学生 \"{name}\" 的密码吗(默认密码: 123456)?",
                    "editSuccess": "编辑学生 \"{name}\" 成功",
                    "resetSuccess": "重置密码成功",
                    "refresh": "刷新成功",
                    "deleteSuccess": "删除成功"
                }
            }
        }
    }
</i18n>
<style>
    
</style>


